export const gimmicks = {
  'box': 'ダンボール',       // 'ダンボール'の英訳は 'cardboard'
  'nuts': 'ピーナッツ',          // 'ピーナッツ'の英訳は 'peanut'
  'mouse': 'ネズミ',               // 'ネズミ'の英訳は 'mouse'
  'wood': '薪',               // '薪'の英訳は 'firewood'
  'chain': '鎖',                   // '鎖'の英訳は 'chain'
  'gift': 'プレゼント箱',       // 'プレゼント箱'の英訳は 'gift box'、変数名としては 'giftBox'
  'jelly': 'ゼリー',// 'ゼリー'の英訳は 'jelly'
  'wine': 'ワイン',                // 'ワイン'の英訳は 'wine'
  'button': 'ボタン',              // 'ボタン'の英訳は 'button'
  'orange': 'みかん箱',       // 'みかん箱'の直訳は難しいですが、'mandarin box'が近いでしょう
  'cobweb': '蜘蛛の巣',            // '蜘蛛の巣'の英訳は 'cobweb'
  'bubble': '泡',                  // '泡'の英訳は 'bubble'
  'ice': '氷',                     // '氷'の英訳は 'ice'
  'printer': '3Dプリンター',     // '3Dプリンター'の英訳は '3D printer'、変数名としては '3DPrinter'
}

const numbers = [1, 2]

const boosters = {
  b: 'ボム',
  r: 'ロケット',
  m: 'ミサイル',
  s: 'スペシャル',
}




Object.entries(gimmicks).forEach(([gKey, gName]) => {
  Object.entries(boosters).forEach(([bKey, bName]) => {
    numbers.forEach(n => {
      ability[gKey + '-' + n + '-' + bKey] = {
        name: gName + 'キラー' + '+' + n + '(' + bName + ')',
        text: bName + 'で' + gName + 'に与える影響が' + n + '段階上昇する\n(効果は最大値が適応される)',
        img: gKey + '-' + bKey,
        killer: gName,
        value: n,
      }
    })
  });
});


const colors = {
  'r': '赤',
  'b': '青',
  'g': '緑',
  'y': '黄',
  'w': '水',
  'p': '紫',
}
const targets = {
  'c': 'カード',
  'd': 'デッキ',
}

const values = {
  'c': [5, 10, 20],
  'd': [3, 5, 10],
}

Object.entries(colors).forEach(([cKey, cName]) => {
  Object.entries(targets).forEach(([tKey, tName]) => {
    Object.values(values[tKey]).forEach(v => {

      ability['scoreUp-' + tKey + '-' + v + '-' + cKey] = {
        name: tName + 'スコアアップ' +v+'%(' + cName + ')',
        text: tKey === 'c'
        ? 'カード自身の' + cName + 'スコアが'+ v +'%アップする\n(同効果は数値を加算した%分上昇する)'
        : 'デッキに編成した際、デッキの' + cName + 'スコアが'+ v +'%アップする\n(同効果は数値を加算した%分上昇する)',


        img: 'scoreUp' + '-' + tKey + '-' + cKey,
      }
    })
  })
})

